// ** React Imports
import { Fragment, useState } from 'react';
import { injectIntl } from 'react-intl';

// ** Modules
import SystemConfiguration from './tabs-content/SystemConfiguration';
import StaffManagement from './tabs-content/StaffManagement';
import WorkingAreaManagement from './tabs-content/WorkingAreaManagement';
import MetaData from './tabs-content/MetaData';
import Commission from './tabs-content/Commission';

// ** Third Party Components
import { Card } from 'reactstrap';
import { Tabs } from 'antd';
// ** Styles
import '@styles/react/libs/react-select/_react-select.scss';
import '@styles/react/libs/tables/react-dataTable-component.scss';
import './../../../@core/scss/react/pages/page-settings.scss';
const { TabPane } = Tabs;
const CustomTable = ({ intl }) => {
  const tabsHeader = [
    {
      tabName:  <div>{intl.formatMessage({ id:'SystemConfiguration' })}</div>,
      tabKey: 1,
    },
    {
      tabName: <div>{intl.formatMessage({ id:'UserManagement' })}</div>,
      tabKey: 2,
      tabChildren: [
        {
          tabName: "Qun l nhn vin",
          tabKey: 2,
        },
        {
          tabName:<div>{intl.formatMessage({ id:'WorkingArea' })}</div>,
          tabKey: 22,
        },
      ],
    },
    {
      tabName: <div>{intl.formatMessage({ id:'MetaData' })}</div>,
      tabKey: 3,
    },
    {
      tabName: <div>{intl.formatMessage({ id:'Commission' })}</div>,
      tabKey: 4,
    },
  ];
  const tabsContent = {
    1: <SystemConfiguration intl={intl}/>,
    2: <StaffManagement intl={intl} />,
    22: <WorkingAreaManagement intl={intl}/>,
    3: <MetaData intl={intl} />,
    4: <Commission intl={intl} />,
  };

  // State activeTab
  const [activeTab, setActiveTab] = useState(tabsHeader[0].tabKey);
  function callback(key) {
    setActiveTab(key);
  }
  return (
    <Fragment>
      <Card>
        <Tabs defaultActiveKey={tabsHeader[0].tabKey} onChange={callback} className="font-tab">
          {tabsHeader.map((tab) => (
            <TabPane tab={tab.tabName} key={tab.tabKey}>
              {
                tab.tabChildren && (
                  <Tabs defaultActiveKey={tab.tabChildren[0].tabKey} onChange={callback} className="tab-child">
                  {
                    tab.tabChildren.map(subTab => (
                      <TabPane tab={subTab.tabName} key={subTab.tabKey}>
                      </TabPane>
                    ))
                  }
                  </Tabs>
                )
              }
            </TabPane>
          ))}
        </Tabs>
      </Card>

      {tabsContent[activeTab] || null}
    </Fragment>
  );
};

export default injectIntl(CustomTable);
