// ** React Imports
import { Fragment, useState, useEffect } from 'react';
import { injectIntl, FormattedMessage } from 'react-intl';

// ** Columns
import { columns } from './../../../../views/pages/settings/columns';

// ** Store & Actions
import { getData } from './../../../../views/apps/user/store/action';
import FakeUserData from './../../../../views/pages/settings/fakeUserData.json';
import { useDispatch, useSelector } from 'react-redux';

// ** Third Party Components
import Select from 'react-select';
import ReactPaginate from 'react-paginate';
import { ChevronDown } from 'react-feather';
import DataTable from 'react-data-table-component';
import { selectThemeColors } from '@utils';
import {
  Card,
  Input,
  Row,
  Col,
  Form,
  Button
} from 'reactstrap';
// ** Styles
import '@styles/react/libs/react-select/_react-select.scss';
import '@styles/react/libs/tables/react-dataTable-component.scss';
import './../../../../@core/scss/react/pages/page-settings.scss';
import {ReactComponent as PlusIcon} from '@src/assets/images/icons/file-icons/fluent_add-12-filled.svg'

// ** Table Header
const CustomHeader = ({
  toggleSidebar,
  handlePerPage,
  rowsPerPage,
  handleFilter,
  searchTerm,
}) => {
  return (
    <div className="invoice-list-table-header w-100 mr-1 ml-50 mt-2 mb-75">
      <Row>
        <Col>
          <div className="add-btn-wrapper" style={{justifyContent: 'flex-start'}}>
            <span className="header">
              <FormattedMessage id="UserList" />
            </span>
          </div>
        </Col>
        <Col>
          <div className="add-btn-wrapper" style={{justifyContent: 'flex-end'}}>
            <Button className="add-btn">
              <span className='add-new-btn'>
              <span className="plus-icon"><PlusIcon/>&ensp;</span>
              <span className='add-user-text'><FormattedMessage id="AddNewUser" /></span>
              </span>
            </Button>
          </div>
        </Col>
      </Row>
    </div>
  );
};

const StaffManagement = ({ intl }) => {
  // ** Store Vars
  const dispatch = useDispatch();
  const store = useSelector((state) => state.users);

  // ** States
  const [searchTerm, setSearchTerm] = useState("");
  const [currentPage, setCurrentPage] = useState(1);
  const [rowsPerPage, setRowsPerPage] = useState(10);
  const [sidebarOpen, setSidebarOpen] = useState(false);
  const [currentRole, setCurrentRole] = useState({value: '', label: <FormattedMessage id="RoleOption" /> });
  const [currentStatus, setCurrentStatus] = useState({value: '', label: <FormattedMessage id="StatusOption" />, number: 0 });

  // ** Function to toggle sidebar
  const toggleSidebar = () => setSidebarOpen(!sidebarOpen);

  // ** Get data on mount
  useEffect(() => {
    dispatch({
      type: "GET_DATA",
      data: FakeUserData.data,
      totalPages: FakeUserData.total,
    });
  }, []);

  // ** User filter options
  const roleOptions = [
    { value: "inputer", label: <FormattedMessage id="InputerRole" /> },
    { value: "checker", label: <FormattedMessage id="CheckerRole" /> },
    { value: "data-admin", label: <FormattedMessage id="DataAdminRole" /> },
    { value: "saller", label: <FormattedMessage id="SallerRole" /> }
  ];

  const statusOptions = [
    { value: "active", label: <FormattedMessage id="Active" />, number: 1 },
    { value: "inactive", label: <FormattedMessage id="Inactive" />, number: 2 },
  ];

  // ** Function in get data on page change
  const handlePagination = (page) => {
    dispatch(
      getData({
        page: page.selected + 1,
        perPage: rowsPerPage,
        role: currentRole.value,
        status: currentStatus.value,
        q: searchTerm,
      })
    );
    setCurrentPage(page.selected + 1);
  };

  // ** Function in get data on rows per page
  const handlePerPage = (e) => {
    const value = parseInt(e.currentTarget.value);
    dispatch(
      getData({
        page: currentPage,
        perPage: value,
        role: currentRole.value,
        status: currentStatus.value,
        q: searchTerm,
      })
    );
    setRowsPerPage(value);
  };

  // ** Function in get data on search query change
  const handleFilter = (val) => {
    setSearchTerm(val);
    dispatch(
      getData({
        page: currentPage,
        perPage: rowsPerPage,
        role: currentRole.value,
        status: currentStatus.value,
        q: val,
      })
    );
  };

  // ** Custom Pagination
  const CustomPagination = () => {
    const count = Number(Math.ceil(store.total / rowsPerPage));

    return (
      <ReactPaginate
        previousLabel={""}
        nextLabel={""}
        pageCount={count || 1}
        activeClassName="active"
        forcePage={currentPage !== 0 ? currentPage - 1 : 0}
        onPageChange={(page) => handlePagination(page)}
        pageClassName={"page-item"}
        nextLinkClassName={"page-link"}
        nextClassName={"page-item next"}
        previousClassName={"page-item prev"}
        previousLinkClassName={"page-link"}
        pageLinkClassName={"page-link"}
        containerClassName={
          "pagination react-paginate justify-content-end my-2 pr-1"
        }
      />
    );
  };

  // ** Table data to render
  const dataToRender = () => {
    const filters = {
      role: currentRole.value,
      status: currentStatus.value,
      q: searchTerm,
    };

    const isFiltered = Object.keys(filters).some(function (k) {
      return filters[k].length > 0;
    });

    if (store.data.length > 0) {
      return store.data;
    } else if (store.data.length === 0 && isFiltered) {
      return [];
    } else {
      return store.allData.slice(0, rowsPerPage);
    }
  };

  return (
    <Fragment>
      <Card className="user-form mt-30">
        <div className="search-area">
          <Col className="d-flex flex-column" sm='12' md='12' lg='12'>
            <div className="search-header">
              {intl.formatMessage({ id: "FilterAndSearch" })}
            </div>
            <Form onSubmit={(e) => e.preventDefault()}>
              <Row>
                <Col className="d-flex flex-column col-6 search-col" sm='6' md='6' lg='3' >
                  <span>{intl.formatMessage({ id: "Search" })}</span>
                  <Input
                    type="text"
                    id="search-input"
                    placeholder={intl.formatMessage({
                      id: "SearchConditions",
                    })}
                  />
                </Col>
                <Col className="d-flex flex-column col-7 desktop-only" sm='6' md='6' lg='3' >
                  <div className='role-text'><span>{intl.formatMessage({ id: "Role" })}</span></div>
                  <Select
                    isClearable={false}
                    theme={selectThemeColors}
                    className="select h-100"
                    classNamePrefix="select"
                    options={roleOptions}
                    value={currentRole}
                    onChange={(data) => {
                      setCurrentRole(data);
                      dispatch(
                        getData({
                          page: currentPage,
                          perPage: rowsPerPage,
                          role: data.value,
                          status: currentStatus.value,
                          q: searchTerm,
                        })
                      );
                    }}
                  />
                </Col>
                <Col className="d-flex flex-column col-7" sm='6' md='6' lg='3'>
                  <span className='status-text'>{intl.formatMessage({ id: "Status" })}</span>
                  <Select
                    theme={selectThemeColors}
                    isClearable={false}
                    className="select h-100"
                    classNamePrefix="select"
                    options={statusOptions}
                    value={currentStatus}
                    onChange={(data) => {
                      setCurrentStatus(data);
                      dispatch(
                        getData({
                          page: currentPage,
                          perPage: rowsPerPage,
                          role: currentRole.value,
                          status: data.value,
                          q: searchTerm,
                        })
                      );
                    }}
                  />
                </Col>

                <Col className="d-flex flex-column col-7" sm='6' md='6' lg='3'>
                  <div className="btn-wrapper">
                    <div className="child">
                      <Button type="button" className="search-btn">
                        {intl.formatMessage({ id: "Search" })}
                      </Button>
                    </div>
                  </div>
                </Col>
              </Row>
            </Form>
          </Col>
        </div>
      </Card>

      <div className='header-wrapper'>
        <CustomHeader
          toggleSidebar={toggleSidebar}
          handlePerPage={handlePerPage}
          rowsPerPage={rowsPerPage}
          searchTerm={searchTerm}
          handleFilter={handleFilter}
        />
      </div>

      <Card className='custom-table mt-10'>
        <DataTable
          noHeader
          pagination
          subHeader
          responsive
          paginationServer
          columns={columns}
          sortIcon={<ChevronDown />}
          className='react-dataTable'
          paginationComponent={CustomPagination}
          data={dataToRender()}
        />
      </Card>
    </Fragment>
  );
};

export default injectIntl(StaffManagement);
