import { useState } from "react";
import { Card } from "reactstrap";
import { Row, Col, Input, Switch, Button } from "antd";
import {AddressFilter} from "../../../../@core/components/filters/address";
import useAreaData from "../../../../services/getAreaData";
const SystemConfiguration = ({ intl }) => {
  const [info, setInfo] = useState({});
  const [config, setConfig] = useState({
    autoUpdateWithUpdatedData: true,
    autoUpdateWithApprovalData: false,
  });
  const { address, cities, dists, blocks, onChangeAddress } = useAreaData();
  const validateHotline = (e) => {
    const inputText = e.target.value;
    var hostlineFormat = /(84|0[3|5|7|8|9])+([0-9]{8})\b/;
    if (inputText.match(hostlineFormat))
      setInfo({ ...info, hotline: e.target.value, isValidHotline: true });
    else setInfo({ ...info, isValidHotline: false });
  };
  const validateEmail = (e) => {
    const inputText = e.target.value;
    var mailformat = /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/;
    if (inputText.match(mailformat))
      setInfo({ ...info, email: inputText, isValidEmail: true });
    else setInfo({ ...info, isValidEmail: false });
  };
  return (
    <div className="sys-config-page">
      <Row justify="end" gutter={[20, 20]}>
        <Col>
          <Button className="btn">CANCEL</Button>
        </Col>
        <Col>
          <Button className="btn-primary btn">SAVE</Button>
        </Col>
      </Row>
      <Card className="mt-30 pd-50-68">
        <div>
          <div>
            <Row className="fn-title">THNG TIN CHUNG</Row>
            <hr />
            <Row>
              <Row className="fn-label">Tn</Row>
              <Input
                onChange={(e) => setInfo({ ...info, name: e.target.value })}
                maxLength={100}
              />
            </Row>
            <Row gutter={[35, 35]}>
              <Col span={12}>
                <Row className="fn-label">Hotline</Row>
                <Input
                  style={{
                    border: `1px solid ${
                      info?.isValidHotline === false ? "red" : "#d9d9d9"
                    }`,
                  }}
                  onChange={validateHotline}
                  maxLength={200}
                />
              </Col>
              <Col span={12}>
                <Row className="fn-label">Email</Row>
                <Input
                  style={{
                    border: `1px solid ${
                      info?.isValidEmail === false ? "red" : "#d9d9d9"
                    }`,
                  }}
                  onChange={validateEmail}
                  maxLength={200}
                />
              </Col>
            </Row>
            <AddressFilter
              onChangeHandle={onChangeAddress}
              address={address}
              cities={cities}
              dists={dists}
              blocks={blocks}
              showLabel={true}
              breakpointResponse={1}
            />
          </div>
        </div>
      </Card>
      <Card className="mt-30  pd-50-68">
        <div>
          <div>
            <Row className="fn-title">CU HNH</Row>
            <hr />
            <Row style={{ marginTop: "26px" }}>
              <Col span={8}>T ng duyt BS qua D liu cp nht</Col>
              <Col>
                <Switch
                  checked={config?.autoUpdateWithUpdatedData}
                  onChange={(checked) =>
                    setConfig({ ...config, autoUpdateWithUpdatedData: checked })
                  }
                />
              </Col>
            </Row>
            <Row style={{ marginTop: "26px" }}>
              <Col span={8}>T ng duyt BS qua D liu xc thc</Col>
              <Col>
                <Switch
                  checked={config?.autoUpdateWithApprovalData}
                  onChange={(checked) =>
                    setConfig({
                      ...config,
                      autoUpdateWithApprovalData: checked,
                    })
                  }
                />
              </Col>
            </Row>
          </div>
        </div>
      </Card>
    </div>
  );
};

export default SystemConfiguration;
