// ** React Imports
import { Link } from 'react-router-dom';

import { FormattedMessage } from 'react-intl';

// ** Custom Components
import Avatar from '@components/avatar';

// ** Store & Actions
import { getUser, editUser, deleteUser } from './../../../views/apps/user/store/action';
import { store } from '@store/storeConfig/store';

// ** Third Party Components
import { Badge } from 'reactstrap';
import { Row, Col } from 'reactstrap';

import { ReactComponent as Edit } from '../../../assets/images/icons/edit.svg';
import { ReactComponent as Trash } from '../../../assets/images/icons/trash.svg';

// ** Renders Client Columns
const renderClient = row => {
  const stateNum = Math.floor(Math.random() * 6),
    states = ['light-success', 'light-danger', 'light-warning', 'light-info', 'light-primary', 'light-secondary'],
    color = states[stateNum]

  if (row.avatar?.length) {
    return <Avatar className='mr-1' img={row.avatar} width='32' height='32' />
  } else {
    return <Avatar color={color || 'primary'} className='mr-1' content={row.fullName || 'John Doe'} initials />
  }
}

// ** Renders Role Columns
const renderRole = (row) => {
  return (
    <span className='text-truncate text-capitalize align-middle'>
      <FormattedMessage id= {row.role} />
    </span>
  )
}

const statusObj = {
  active: 'light-success',
  inactive: 'light-danger'
}

const onDeleteUser = row => {
  store.dispatch(deleteUser(row.id))
}

export const columns = [
  {
    name: <FormattedMessage id='UserColumn'/>,
    selector: 'fullName',
    sortable: true,
    cell: row => (
      <div className='d-flex justify-content-left align-items-center w-100'>
        {renderClient(row)}
        <div className='d-flex flex-column w-100'>
          <Link
            to={`/apps/user/view/${row.id}`}
            className='user-name mt-1 mb-0'
            onClick={() => store.dispatch(getUser(row.id))}
          >
            <span className='font-weight-bold' style={{overflowWrap: 'break-word'}}>{row.fullName}</span>
          </Link>
          <small className='text-truncate text-muted mb-0'>ID: {row.id?.toString().padStart(6, 0)}</small>
        </div>
      </div>
    )
  },
  {
    name: <FormattedMessage id='PhoneColumn'/>,
    selector: 'phone',
    sortable: true,
    cell: row => row.contact
  },
  {
    name: <FormattedMessage id='EmailColumn'/>,
    selector: 'email',
    sortable: true,
    cell: row => row.email
  },
  {
    name: <FormattedMessage id='Role'/>,
    selector: 'role',
    sortable: true,
    cell: row => renderRole(row)
  },
  {
    name: <FormattedMessage id='Status'/>,
    selector: 'status',
    sortable: true,
    cell: row => (
      <Badge className='text-capitalize' color={statusObj[row.status]} pill>
        <FormattedMessage id= {row.status} />
      </Badge>
    )
  },
  {
    name: <FormattedMessage id='ActionsColumn'/>,
    cell: row => (
        <Row>
            <Col md='6'>
              <span onClick={() => store.dispatch(editUser(row.id))}><Edit /></span>
            </Col>
            <Col md='6'>
              <span onClick={() => onDeleteUser(row)}><Trash /></span>
            </Col>
        </Row>
    )
  }
]