// ** React Imports
import { Fragment, useState, useEffect } from "react";
import { injectIntl, FormattedMessage } from "react-intl";

// ** Columns
import { columns } from "@src/views/pages/settings/columnsWorkingArea";

// ** Store & Actions
import { getData } from "@src/views/apps/user/store/action";
import FakeWorkingAreaData from "@src/views/pages/settings/fakeWorkingAreaData.json";
import { useDispatch, useSelector } from "react-redux";

// ** Third Party Components
import DataTable from "react-data-table-component";
import { Card, Row, Col, Button } from "reactstrap";
import { Row as RowAnt, Modal, Select } from "antd";
// ** Styles
import "@styles/react/libs/react-select/_react-select.scss";
import "@styles/react/libs/tables/react-dataTable-component.scss";
import "@src/@core/scss/react/pages/page-settings.scss";
import { AddressFilter } from "@src/@core/components/filters/address";
import useAreaData from "@src/services/getAreaData";
import useStaffList from "@src/services/getStaffList";

import { ReactComponent as SearchIcon } from "@src/assets/images/icons/file-icons/search-normal.svg";
import { ReactComponent as PlusIcon } from "@src/assets/images/icons/file-icons/fluent_add-12-filled.svg";
import { ReactComponent as Zeit } from "@src/assets/images/icons/file-icons/zeit.svg";
import { ReactComponent as Check } from "@src/assets/images/icons/file-icons/check.svg";

// ** Table Header
const CustomHeader = ({
  toggleSidebar,
  handlePerPage,
  rowsPerPage,
  handleFilter,
  searchTerm,
}) => {
  return (
    <div className="invoice-list-table-header w-100 mr-1 ml-50 mt-2 mb-75">
      <Row>
        <Col>
          <div
            className="add-btn-wrapper"
            style={{ justifyContent: "flex-start" }}
          >
            <span className="header">DANH SCH KHU VC</span>
          </div>
        </Col>
      </Row>
    </div>
  );
};

const WorkingAreaManagement = ({ intl }) => {
  // ** Store Vars
  const dispatch = useDispatch();
  const store = useSelector((state) => state.users);

  // ** States
  const [searchTerm, setSearchTerm] = useState("");
  const [currentPage, setCurrentPage] = useState(1);
  const [rowsPerPage, setRowsPerPage] = useState(10);
  const [sidebarOpen, setSidebarOpen] = useState(false);
  const [currentRole, setCurrentRole] = useState({
    value: "",
    label: <FormattedMessage id="RoleOption" />,
  });
  const [currentStatus, setCurrentStatus] = useState({
    value: "",
    label: <FormattedMessage id="StatusOption" />,
    number: 0,
  });

  const { address, cities, dists, blocks, onChangeAddress } = useAreaData();
  const {
    address: address_2,
    cities: cities_2,
    dists: dists_2,
    blocks: blocks_2,
    onChangeAddress: onChangeAddress_2,
  } = useAreaData();

  // ** Function to toggle sidebar
  const toggleSidebar = () => setSidebarOpen(!sidebarOpen);

  // ** Get data on mount
  useEffect(() => {
    const temp = FakeWorkingAreaData.data.map((obj, index) => ({
      ...obj,
      stt: index + 1,
    }));
    dispatch({
      type: "GET_DATA",
      data: temp,
      totalPages: FakeWorkingAreaData.total,
    });
  }, []);
  // ** Function in get data on rows per page
  const handlePerPage = (e) => {
    const value = parseInt(e.currentTarget.value);
    dispatch(
      getData({
        page: currentPage,
        perPage: value,
        role: currentRole.value,
        status: currentStatus.value,
        q: searchTerm,
      })
    );
    setRowsPerPage(value);
  };

  // ** Function in get data on search query change
  const handleFilter = (val) => {
    setSearchTerm(val);
    dispatch(
      getData({
        page: currentPage,
        perPage: rowsPerPage,
        role: currentRole.value,
        status: currentStatus.value,
        q: val,
      })
    );
  };

  // ** Table data to render
  const dataToRender = () => {
    const filters = {
      role: currentRole.value,
      status: currentStatus.value,
      q: searchTerm,
    };

    const isFiltered = Object.keys(filters).some(function (k) {
      return filters[k].length > 0;
    });

    if (store.data.length > 0) {
      return store.data;
    } else if (store.data.length === 0 && isFiltered) {
      return [];
    } else {
      return store.allData.slice(0, rowsPerPage);
    }
  };
  const staffList = useStaffList();
  const [isShowAddWorkingAreaModal, setIsShowAddWorkingAreaModal] =
    useState(false);
  const showAddWorkingModal = (_) => {
    setIsShowAddWorkingAreaModal(!isShowAddWorkingAreaModal);
  };
  const addWorkingModal = (_) => { };
  const generateSelector = (placeholder, type, listOptions) => {
    return (
      <Col className="input-address-custom staff-mng-popup">
        <Select
          onChange={onChangeAddress_2}
          placeholder={placeholder}
          value={address_2[type]}
          suffixIcon={<Zeit />}
          showSearch
          style={{ width: "100%" }}
          filterOption={(input, option) =>
            option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
          }
        >
          {listOptions !== undefined &&
            listOptions.map((obj) => (
              <Select.Option value={obj.name} key={obj.areaDataId} type={type}>
                {obj.areaDataName}
              </Select.Option>
            ))}
        </Select>
      </Col>
    );
  };
  return (
    <Fragment>
      <div className="add-btn-wrapper" style={{ justifyContent: "flex-end" }}>
        <Button className="add-btn" onClick={showAddWorkingModal}>
          <span className="add-new-btn">
            <div className="plus-icon">
              <PlusIcon />
              &ensp;
            </div>
            <div className="add-user-text">THM KHU VC</div>
          </span>
        </Button>
      </div>
      <Modal
        closable={false}
        className="modal-working-area staff-mng-popup-area"
        visible={isShowAddWorkingAreaModal}
        title="Thm Mi Khu Vc Lm Vic"
        onOk={addWorkingModal}
        footer={[
          <Button onClick={showAddWorkingModal} className="btn-sec">
            HY
          </Button>,
          <Button onClick={addWorkingModal} className="btn-pri" >
            TO KHU VC
          </Button>,
        ]}
      >
        <RowAnt>
          <Col>
            <label>
              Tn khu vc <span>*</span>
            </label>
            <br />
            <input type="text" placeholder="Tn khu vc" required></input>
          </Col>
          <Col className="area-mng-col">
            <label>
              Tnh / Thnh ph <span>*</span>
            </label>
            <br />
            {generateSelector("--- Chn tnh/thnh ph ---", "city", cities_2)}
          </Col>
        </RowAnt>
        <RowAnt>
          <Col className="area-mng-col">
            <label>
              Qun / Huyn <span>*</span>
            </label>
            <br />
            {generateSelector("--- Chn qun/huyn ---", "dist", dists_2)}
          </Col>
          <Col className="area-mng-col">
            <label>
              Phng / X <span>*</span>
            </label>
            <br />
            {generateSelector("--- Chn phng/x ---", "block", blocks_2)}
          </Col>
        </RowAnt>
        <RowAnt style={{ width: '98%' }} justify="center">
          <Col className="area-mng-col">
            <label>Chn nhn vin</label>
            <br />
            <Select
              placeholder="--- Chn nhn vin cho khu vc lm vic ---"
              suffixIcon={<Zeit />}
              showSearch
              className="select-staff"
              filterOption={(input, option) =>
                option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
              }
            >
              {staffList.map((staff) => (
                <Select.Option>
                  <RowAnt>
                    <Col span={6}><b>{staff.firstName + " " + staff.lastName}</b></Col>
                    <Col span={12}>{staff.phone}</Col>
                  </RowAnt>
                  <RowAnt>
                    <Col span={6}>ID: #<a href="/">{staff.staffId}</a></Col>
                    <Col span={12}>{staff.email}</Col>
                  </RowAnt>
                </Select.Option>
              ))}
            </Select>
          </Col>
        </RowAnt>
      </Modal>
      <Card className="user-form mt-30">
        <RowAnt gutter={[35, 35]} style={{ padding: "31px 48px" }}>
          <AddressFilter
            showLabel="true"
            prefixInput={<SearchIcon />}
            placeholderInput="Tm kim theo tn khu vc"
            onChangeHandle={onChangeAddress}
            address={address}
            cities={cities}
            dists={dists}
            blocks={blocks}
          />
        </RowAnt>
      </Card>

      <div className="header-wrapper">
        <CustomHeader
          toggleSidebar={toggleSidebar}
          handlePerPage={handlePerPage}
          rowsPerPage={rowsPerPage}
          searchTerm={searchTerm}
          handleFilter={handleFilter}
        />
      </div>

      <Card className="custom-table mt-10">
        <DataTable
          noHeader
          columns={columns}
          data={dataToRender()}
          pagination
        />
      </Card>
    </Fragment>
  );
};

export default injectIntl(WorkingAreaManagement);
