import { useState, useEffect } from "react";
import { HOST } from "@src/constants/url";
import { ENDPOINTS } from "@src/constants/endpoints";
export default function useAreaData() {
  const [address, setAddress] = useState({});
  const [parentID, setParentID] = useState({ country: 1 });
  const [dataArea, setDataArea] = useState({
    city: new Map(),
    dist: new Map(),
    block: new Map(),
  });
  const fetchDataArea = async (parentType, childType) => {
    const axios = require("axios");
    axios
      .post(`${HOST}${ENDPOINTS.GET_AREA_DATA}`, {
        areaParentId: parentID[parentType],
      })
      .then(function (response) {
        const newDataArea = { ...dataArea };
        newDataArea[childType].set(
          parentID[parentType],
          response.data.data.data
        );
        setDataArea(newDataArea);
      })
      .catch(function (error) {
        console.log(error);
      });
  };
  useEffect(() => {
    dataArea.city.get(parentID.country) ?? fetchDataArea("country", "city");
    dataArea.dist.get(parentID.city) ?? fetchDataArea("city", "dist");
    dataArea.block.get(parentID.dist) ?? fetchDataArea("dist", "block");
  }, [parentID]);
  const onChangeAddress = (value, e) => {
   const obj_temp = {
        ...address,
        [e.type]: value,
        [e.type+"Name"]:e.children
      };
      switch (e.type) {
        case "city":
          obj_temp.dist = obj_temp.block = {};
          setParentID({ ...parentID, city: +e.key, dist: -1, block: -1 });
          break;
        case "dist":
          obj_temp.block = {};
          setParentID({ ...parentID, dist: +e.key, block: 0 });
          break;
        default:
      }
    setAddress(obj_temp);
  };

  return {
    address,
    cities: dataArea.city.get(parentID.country),
    dists: dataArea.dist.get(parentID.city),
    blocks: dataArea.block.get(parentID.dist),
    onChangeAddress,
    setParentID,
    setAddress,
  };
}
