import { ReactComponent as Zeit } from "../../../assets/images/icons/file-icons/zeit.svg";
import { Select, Input, Row, Col } from "antd";
import React from "react";
import "antd/dist/antd.css";
import "@src/@core/scss/base/components/select.scss";
const { Option } = Select;
export const AddressFilter= React.memo(props=> {

  const { cities, dists, blocks, address, onChangeHandle } = props;
  const generateSelector = (placeholder, type, listOptions) => {
    return (
      <Col
        span={props.breakpointResponse ? 8 : 6}
        className="input-address-custom"
      >
        {props.showLabel && <Row className="fn-label">{placeholder}</Row>}
        <Select
          onChange={onChangeHandle}
          placeholder={placeholder}
          value={address[type]}
          suffixIcon={<Zeit />}
          showSearch
          style={{ width: "100%" }}
          filterOption={(input, option) =>
            option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
          }
        >
          {listOptions !== undefined &&
            listOptions.map((obj) => (
              <Option value={obj.name} key={obj.areaDataId} type={type}>
                {obj.areaDataName}
              </Option>
            ))}
        </Select>
      </Col>
    );
  };
  return (
    <>
      <Col
        span={props.breakpointResponse ? 24 : 6}
        className="input-address-custom"
      >
        {props.showLabel && <Row className="fn-label">a ch</Row>}
        <Input
          placeholder={props.placeholderInput??"Nhp a ch & tn ng"}
          onChange={(e)=>onChangeHandle(e.target.value,{type:"street",key:-1})}
          value={address.street}
          maxLength={200}
          prefix={props.prefixInput}
        />
      </Col>
      {props.breakpointResponse ? (
        <Row gutter={[63.68,63.68]}>
          {generateSelector("Tnh/ Thnh", "city", cities)}
          {generateSelector("Qun/ Huyn", "dist", dists)}
          {generateSelector("Phng X", "block", blocks)}
        </Row>
      ) : (
        <>
          {generateSelector("Tnh/ Thnh", "city", cities)}
          {generateSelector("Qun/ Huyn", "dist", dists)}
          {generateSelector("Phng X", "block", blocks)}
        </>
      )}
    </>
  );
})
AddressFilter.defaultProps = {
  cities: [],
  dists: [],
  blocks: [],
  address: {},
  onChangeHandle: () => {},
};
